  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>{{sourceType ===1 ? '销售' : '库存'}}管理</el-breadcrumb-item>
      <el-breadcrumb-item>销售出库管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">销售出库</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>销售出库单详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div id="leixin">
      <!-- <h3 class="title_container">销售出库单详情</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="销售组织：" size="mini">
            <span>{{params.company_name}}</span>
          </el-form-item>
          <el-form-item label="单据号：" size="mini">
            <span>{{params.outstock_sn}}</span>
          </el-form-item>
          <el-form-item label="业务日期：" size="mini">
            <span>{{params.business_time}}</span>
          </el-form-item>
          <el-form-item label="客户名称：" size="mini">
            <span>{{params.customer_name}}</span>
          </el-form-item>
          <el-form-item label="部门：" size="mini">
            <span>{{params.department_name}}</span>
          </el-form-item>
          <el-form-item label="业务员：" size="mini">
            <span>{{params.business_name}}</span>
          </el-form-item>
          <el-form-item label="收货人：" size="mini">
            <span>{{params.consignee_name}}</span>
          </el-form-item>
          <el-form-item label="电话：" size="mini">
            <span>{{params.consignee_mobile}}</span>
          </el-form-item>
          <el-form-item label="所属区域：" size="mini">
            <span>{{params.province_name}}&nbsp;&nbsp;{{params.city_name}}&nbsp;&nbsp;{{params.area_name}}</span>
          </el-form-item>
          <el-form-item size="mini" label="详细地址：">
            <span>{{params.address}}</span>
          </el-form-item>
          <el-form-item size="mini" label="关联单据号：">
            <!-- <span>{{params.order_sn}}</span> -->
            <el-button @click="goHref(params)" type="text" size="small">
              {{params.order_sn}}
            </el-button>
          </el-form-item>
          <el-form-item label="回款方式：" size="mini">
            <span v-if="params.payment_method==1">客户自结</span>
            <span v-if="params.payment_method==2">物流代收</span>
            <span v-if="params.payment_method==3">电商结算</span>
          </el-form-item>
          <el-form-item label="物流公司：" size="mini">
            <span>{{params.settlement_customer_name}}</span>
          </el-form-item>
          <!-- <el-form-item label="意向物流：" size="mini">
            {{params.intentional_logistics}}
          </el-form-item> -->
          <el-form-item label="送货需求：" size="mini">
            <span>{{params.delivery_demand_name}}</span>
          </el-form-item>
          <el-form-item label="交货方式：" size="mini">
            {{params.hand_over_name}}
          </el-form-item>
          <el-form-item label="出库类型：" size="mini">
            <span>{{params.outstock_type_name}}</span>
          </el-form-item>
          <el-form-item label="仓库名称：" size="mini">
            <span>{{params.warehouse_name}}</span>
          </el-form-item>
          <el-form-item label="打印次数：" size="mini">
            <span>{{params.print_times}}</span>
          </el-form-item>
          <el-form-item label="单据状态" size="mini">
            {{params.auditor_status_name}}
          </el-form-item>
          <el-form-item label="摘要：">
            {{params.abstract}}
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <xTable :isShowTool='true' :isList='true' ref='xTable' :footerMethod='footerMethod' :tableColumn='tableColumn' :xData='tableData' />
        <el-form class="from_table">
          <el-form-item label="出库金额:" size="mini">
            <span>{{params.outstock_price?params.outstock_price:0|priceFormat }}元</span>
          </el-form-item>
          <el-form-item label="使用返利:" size="mini">
            <span>{{params.rebate_price?params.rebate_price:0|priceFormat}}</span>
          </el-form-item>
          <el-form-item label="使用授信:" size="mini">
            <span>{{params.credit_price?params.credit_price:0|priceFormat}}</span>
          </el-form-item>
          <el-form-item label="使用预存款:" size="mini">
            <span>{{params.deposits_price?params.deposits_price:0|priceFormat}}</span>
          </el-form-item>
          <el-form-item label="结算金额:" size="mini">
            <span>{{params.settlement_price?params.settlement_price:0|priceFormat}}</span>
          </el-form-item>
        </el-form>
      <div>
        <span style='font-size: 14px;'>开票信息：</span>
        <el-input type='textarea' v-model="params.invoice_info"  disabled style='width: 50%;vertical-align: text-top;'></el-input>
      </div>
        <div class="refundauditFoot">
          <span>制单人：{{params.add_user_name}}</span>
          <span>单据时间：{{params.add_time}}</span>
          <span>审核人：{{params.auditor_name}}</span>
          <span>审核时间：{{params.auditor_time}}</span>
          <span>修改人：{{params.update_user_name}}</span>
          <span>修改时间：{{params.update_time}}</span>
        </div>
      </div>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
      <template v-if="params.printStatus">
        <el-button type="primary" @click="lodopDe('salesPickingOrders')" v-if="authority.b_printingpick && params.auditor_status == 2">拣货单打印</el-button>
        <el-button @click="lodopDe('outboundOrder')" v-if="authority.b_printingoutstock==1 && params.auditor_status == 2">出库单打印</el-button>
        <el-button @click="lodopDe('autoPartsList')" v-if="authority.b_printingpartslist==1 && params.auditor_status == 2">汽配单打印</el-button>
      </template>
      <template v-else>
        <el-button type="primary" @click="lodopDe('salesPickingOrders')" v-if="authority.b_printingpick == 1 ">拣货单打印</el-button>
        <el-button @click="lodopDe('outboundOrder')" v-if="authority.b_printingoutstock==1">出库单打印</el-button>
        <el-button @click="lodopDe('autoPartsList')" v-if="authority.b_printingpartslist==1">汽配单打印</el-button>
      </template>

      <template v-if="params.auditor_status == 2">
        <!-- 已审核 -->
        <el-button plain @click="auditOrUnaudit('salesAuditorNoPass')" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
      </template>
      <!-- 未审核 -->
      <template v-if="params.auditor_status == 1">

        <el-button type="primary" @click="shenhe('salesOutstockAuidtorPass')" v-if="authority.b_pass==1">审核</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="params.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
    <div v-if="printingStatusGet">
      <lodopDelivery ref="lodopDelivery" :title='title'></lodopDelivery>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import mixins from "./salesoutSeeCommon.js";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //出库单打印组件
import { repertory } from "common/utils/public_function/commonUtils";
export default {
  mixins: [mixins],
  data() {
    return {
      authority: {},
      colorFlag: [],
      sum: null,
      title: null,
      tableData: [],
      params: {},
      hasAuditorStatusPrint: null
    };
  },
  props: ["returnUrl", "sourceType"],
  computed: {
    ...mapGetters(["printingStatusGet"])
  },
  methods: {
    ...mapActions(["salesorderSee", "printingStatus"]),
    goHref(row) {
      this.$router.push({
        path: "/sales/salesorder/salesorderSee"
      });
      this.salesorderSee({
        order_id: row.order_id
      });
    },
    addF() {
      if (this.sourceType === 1) {
        this.$router.push({ path: "/sales/salesout/salesoutAdd" });
      } else {
        this.$router.push({
          path: "/inventory/salesoutstorage/instroageSalesoutAdd"
        });
      }
    },
    goBack() {
      if (this.returnUrl) {
        this.$router.push(this.returnUrl);
      } else {
        this.$router.back(-1);
      }
    },
    lodopDe(name) {
      this.printingStatus(true);
      this.$set(this, "title", name);
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf({
          outstock_id: this.params.outstock_id
        });
      });
    },
    auditOrUnaudit(api) {
      let vm = this;
      if (api == "salesOutstockAuidtorPass") {
        // 校验对接车郎中
        this.$baseData
          .checkCarDoctor({
            bill_type: 9,
            bill_id: vm.params.outstock_id,
            operation_type: 4
          })
          .then(() => {
            //校验安全库存金额
            this.$axMethod
              .safetyStockCheck({
                bill_logogram: "xsck",
                order_type: 2,
                bill_id: vm.params.outstock_id
              })
              .then(() => {
                repertory(
                  this,
                  {
                    goods_info: vm.params.outstock_id,
                    status: 2,
                    type: 1
                  },
                  function() {
                    vm.$axios({
                      method: "post",
                      url: vm.$api[api],
                      data: {
                        outstock_id: vm.params.outstock_id
                      }
                    }).then(res => {
                      if (res.data.code == 200) {
                        vm.$message({
                          message: res.data.msg || res.data.data,
                          type: "success"
                        });
                        vm.goBack();
                      }
                    });
                  }
                );
              });
          });
      } else {
        // 校验对接车郎中
        this.$baseData
          .checkCarDoctor({
            bill_type: 9,
            bill_id: this.params.outstock_id,
            operation_type: 5
          })
          .then(() => {
            this.$axios({
              method: "post",
              url: this.$api[api],
              data: {
                outstock_id: this.params.outstock_id
              }
            }).then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg || res.data.data,
                  type: "success"
                });
                this.goBack();
              }
            });
          });
      }
    },
    shenhe(api) {
      var addr = [];
      for (var i = 0; i < this.tableData.length; i++) {
        addr.push({
          order_id: this.params.outstock_id,
          serial_number: i
        });
      }
      let obj = {
        company_id: this.params.company_id,
        bill_id: JSON.stringify(addr),
        operation: 2,
        bill_type: 2,
        is_info: 1
      };
      this.$axMethod.v2setBillCostControl(obj).then(res => {
        let s = res.data.data;
        if (res.data.code == 200) {
          if (s.length > 0) {
            if (s[0].bill_setting_status == 0) {
              //提醒逻辑
              let test = "";
              s.forEach((item, index) => {
                test += index + 1 + "." + item.name + "<br>";
              });
              this.$confirm(
                ` <div class="colorblue">${test}</div> <br>以上商品售价低于成本价，是否继续提交？`,
                "提示",
                {
                  confirmButtonText: "确定",
                  cancelButtonText: "取消",
                  dangerouslyUseHTMLString: true,
                  showClose: false,
                  type: "warning",
                  distinguishCancelAndClose: true // 将取消（点击取消按钮）与关闭（点击关闭按钮或遮罩层、按下 ESC 键）进行区分
                }
              )
                .then(() => {
                  this.auditOrUnaudit(api);
                })
                .catch(action => {
                  if (action == "cancel") {
                    // 点击否
                  }
                });
            } else {
              // 禁止逻辑
              this.$message({
                message: "商品售价低于成本价，审核失败！",
                type: "warning"
              });
              s.forEach(item => {
                this.colorFlag.push(item.serial_number);
              });

              this.colorFlag.forEach(item => {
                this.tableData[item].canNotDelete = true;
              });
              this.tableData.push({});
              this.tableData.pop();
            }
          } else {
            this.auditOrUnaudit(api);
          }
        }
      });
    }
  },
  components: {
    lodopDelivery
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.params.outstock_id = this.$route.query && this.$route.query.id;
    if (this.params.outstock_id || this.$route.query.outstock_sn) {
      this.$axios({
        method: "get",
        url: this.$api.salesCheckOutstock,
        params: {
          outstock_id: this.params.outstock_id,
          outstock_sn: this.$route.query.outstock_sn
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.params = res.data.data;
          this.params.printStatus =
            this.$route.query && this.$route.query.printStatus;
          this.tableData = res.data.data.goods_info;
        }
      });
    } else {
      this.$message.error("单据信息错误");
      this.goBack();
    }
  }
};
</script>

